export function AllFait(): FIAT[] {
    return ALLFIAT;
}
export function getFait(symbol: string): (FIAT | undefined) {
    let re: FIAT | undefined;
    ALLFIAT.forEach(element => {
        if (element.symbol === symbol) re = element;
    });
    return re

}




